.tableheading {
  h5 {
    color: #211b3a;
    font-family: Urbanist;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: 28px;
    /* 140% */
  }
}

.table th {
  font-family: Urbanist;
  color: #492fb2;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
  /* 114.286% */
  letter-spacing: 0.07px;
}

.table td {
  color: #333;
  font-family: Urbanist;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 150%;
  /* 21px */
  letter-spacing: 0.25px;
}

.custom-table {
  border: 1px solid #e5e7eb;
  border-radius: 8px;

  thead th {
    background-color: #eee;
    padding: 12px;
  }
}

.tableprofile{
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  object-position: top;
}

.badge {
  border-radius: 8px;
  font-family: "Noto Sans";
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: var(--Line-Heights-10, 20px);
  /* 166.667% */
  letter-spacing: var(--Letter-Spacing-4, 0.25px);
  padding: 6px 12px;
}


.dropdown-menu {
  border-radius: 8px;
  border: 1px solid #c6c6c6;
  background: #fff;
  cursor: pointer;
  padding: 8px 0px;

  .dropdown-item {
    padding: 8px 16px;
    display: flex;
    align-items: center;
    gap: 8px;
    font-family: Urbanist;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
  }

  .blue {
    color: #3C9718;
  }

  .black {
    color: #211b3a;
  }

  .red {
    color: #b7131a;
  }
}

.btn-check:checked+.btn,
.btn.active,
.btn.show,
.btn:first-child:active,
:not(.btn-check)+.btn:active {
  border-color: transparent;
}


/* Modern pagination style */
.modern-pagination .page-item {
  margin: 0 3px;
}

.modern-pagination .page-link {
  border-radius: 8px !important;
  border: 1px solid #ddd;
  padding: 6px 12px;
  color: #444;
  background-color: #fff;
  transition: all 0.2s ease;
  cursor: pointer;
}

.modern-pagination .page-link.active {
  background-color: #f5f0ff;
  /* light purple */
  border: 1px solid #7c3aed;
  /* purple border */
  color: #7c3aed;
  /* purple text */
  font-weight: 600;
}

.modern-pagination .page-link:hover {
  background-color: #f2f2f2;
  color: #000;
}

.modern-pagination .page-item.disabled .page-link {
  opacity: 0.5;
  pointer-events: none;
}
.form-select {
  display: block;
  width: 100%;
  padding: 0.375rem 2.25rem 0.375rem 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: var(--bs-body-color);
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-color: var(--bs-body-bg);
  background-image: var(--bs-form-select-bg-img), var(--bs-form-select-bg-icon, none);
  background-repeat: no-repeat;
  background-position: right 0.75rem center;
  background-size: 16px 12px;
  border: var(--bs-border-width) solid var(--bs-border-color);
  border-radius: var(--bs-border-radius);
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  cursor: pointer;
}